import db from 'db';

async function getData(tableName) {
  const rows = await db.collageDB[tableName].toArray();
  if (rows.length) return rows;
  return [];
}

function clearData(tableName) {
  return db.collageDB[tableName].clear();
}

async function getCollageDetails() {
  const rows = await getData('details');
  console.log('in get collage details', rows);
  if (rows.length) return rows[0];
  return {};
}

async function getCollageData() {
  const details = await getCollageDetails();
  const images = await getData('images');
  return { details, images };
}

function clearCollageData() {
  clearData('details');
  clearData('images');
}

async function save(data, tableName) {
  return data.map((dt) => db.collageDB[tableName].put(dt));
}

export default { getCollageData, clearCollageData, save };
