const parseCookie = (str) =>
  Object.fromEntries(str.split(';').map((cookie) => cookie.split('=', 2)));

function getUserInfo() {
  const parsedCookies = parseCookie(document.cookie);
  if ('userInfo' in parsedCookies) {
    return JSON.parse(atob(parsedCookies.userInfo));
  }
  return '';
}

function handleMaxPhotos(containerWidth) {
  if (containerWidth < 450) return { maxPhotos: 2 };
  if (containerWidth < 800) return { maxPhotos: 3 };
  return { maxPhotos: 4 };
}

function handleColumns(containerWidth) {
  if (containerWidth < 450) return 2;
  if (containerWidth < 800) return 3;
  return 4;
}

function handleArrangement(images, callback) {
  let twidth = 0;
  let theight = 0;
  images.forEach(({ height, width }) => {
    if (height > width) theight += 1;
    else twidth += 1;
  });
  if (twidth > theight) {
    callback('rows');
  } else {
    callback('columns');
  }
}

async function isFieldValid(key, value, setFieldError, schema) {
  let hasError = false;
  const testField = await schema
    .validate({ [key]: value }, { abortEarly: false })
    .catch((error) => error);
  setFieldError('');
  if (testField.inner) {
    testField.inner.forEach((error) => setFieldError(error.message));
    hasError = true;
  }
  return !hasError;
}

export default {
  parseCookie,
  getUserInfo,
  handleMaxPhotos,
  handleColumns,
  handleArrangement,
  isFieldValid,
};
