import PropTypes from 'prop-types';
import { useState } from 'react';
import { Navigate, useLocation } from 'react-router-dom';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import Button from '@mui/material/Button';
import commonUtils from '../utils';

function AuthDialog({ open, onLogin }) {
  console.log('auth dialog', open);
  return (
    <Dialog open={open} fullWidth>
      <DialogTitle sx={{ textAlign: 'center' }}>Login</DialogTitle>
      <DialogContent
        dividers
        sx={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'space-between',
        }}
      >
        Please login to your account to complete this action
      </DialogContent>
      <DialogActions>
        <Button
          variant="outlined"
          onClick={onLogin}
          sx={{ color: 'primary', fontWeight: 'bold' }}
        >
          Login
        </Button>
      </DialogActions>
    </Dialog>
  );
}
AuthDialog.propTypes = {
  open: PropTypes.bool.isRequired,
  onLogin: PropTypes.func.isRequired,
};

function Authenticate({ children, nextPath }) {
  const [open, setOpen] = useState(true);
  const authURL = process.env.REACT_APP_AUTH_URL;
  const location = useLocation();
  const cookieValue = commonUtils.getUserInfo();
  const isLoggedIn = cookieValue !== '';

  console.log('auth', open);
  if (isLoggedIn === true) {
    return children;
  }

  const redirectToLogin = () => {
    const path = `https://${authURL}/oauth2/authorize?next=${location.pathname}${location.search}`;
    window.location.replace(path);
  };

  if (nextPath) {
    return <Navigate to={nextPath} replace />;
  }

  return (
    <AuthDialog
      open={open}
      onLogin={redirectToLogin}
      onClose={() => setOpen(false)}
    />
  );
}

Authenticate.defaultProps = {
  nextPath: '',
};

Authenticate.propTypes = {
  children: PropTypes.node.isRequired,
  nextPath: PropTypes.string,
};

export default Authenticate;
