import PropTypes from 'prop-types';
import db from 'db';
import { useEffect, useState } from 'react';
import dbutils from 'components/collage/dbutils';
import useBase from 'hooks/collage/base';
import { useNavigate } from 'react-router-dom';

import Authenticate from 'hocs/require-authentication';
import Collage from 'components/collage/Collage';
import DownloadDialog from 'components/collage/DownloadDialog';
import SaveDialog from 'components/collage/SaveDialog';
import SaveStatusDialog from 'components/collage/SaveStatusDialog';
import Header from 'components/collage/Header';
import Loading from 'components/Loading';
import View from 'components/collage/View';

import DesktopActions from 'components/collage/DesktopActions';
import DesktopControls from 'components/collage/DesktopControls';
import DesktopImageOptions from 'components/collage/DesktopImageOptions';
import EditControl from 'components/collage/EditControl';
import MobileActions from 'components/collage/MobileActions';
import MobileControls from 'components/collage/MobileControls';

export default function BaseCollage({ getCollage, clearCollage, saveType }) {
  console.log('base collage', saveType);
  const navigate = useNavigate();
  const [details, setDetails] = useState({});
  const [status, setStatus] = useState('fetching');

  const [
    getDetails,
    collageName,
    ,
    collageID,
    constants,
    dimensionProps,
    optionProps,
    actionProps,
    imageProps,
    treeProps,
    handleClear,
    collageCallback,
    handleRemoveImage,
    newCollage,
    saveStatus,
    saveCollage,
  ] = useBase(details);

  useEffect(async () => {
    if (Object.keys(details).length === 0) {
      setStatus('fetching');
      const collageDetails =
        saveType === 'new'
          ? await getCollage()
          : await getCollage(
              imageProps.setImageCount,
              imageProps.setNewImageCount,
              imageProps.handleImageData
            );
      setStatus('fetch end');
      console.log('setting collage details', collageDetails);
      setDetails(collageDetails);
      if (saveType === 'new') {
        clearCollage();
      }
    }
  }, []);

  console.log(
    'base collage',
    details,
    imageProps.imgCount,
    imageProps.loaded,
    imageProps.images,
    imageProps.newImagesLoaded,
    collageName,
    collageID
  );

  useEffect(() => {
    let st = status;
    // if (st === 'fetching') return;
    if (
      imageProps.imgCount === 0 &&
      Object.keys(details).length === 0 &&
      st === 'fetch end'
    ) {
      st = 'empty';
    }
    if (
      imageProps.images.length > 0 &&
      imageProps.images.length === imageProps.imgCount
    ) {
      st = 'preparing';
    }
    if (imageProps.images.length < imageProps.imgCount) {
      st = 'loading';
    }
    if (imageProps.loaded >= imageProps.imgCount && imageProps.imgCount > 0) {
      st = 'ready';
    }
    setStatus(st);
    console.log(
      'status calculation',
      imageProps.loaded,
      imageProps.imgCount,
      imageProps.images.length,
      st,
      details
    );
  }, [imageProps]);

  const clearAll = () => {
    setDetails({});
    handleClear();
    clearCollage();
    setStatus('fetch end');
    if (saveType === 'existing') {
      setTimeout(() => navigate('/collages'), 3000);
    }
  };

  const handleSave = async (name) => {
    console.log('in handle save', getDetails(name), imageProps.images);

    actionProps.onSaveClick();
    const res =
      saveType === 'new' ? await newCollage(name) : await saveCollage(name);
    console.log('save results', res, collageID);
    if (res) {
      setTimeout(() => {
        if (saveType === 'new') clearAll();
        actionProps.setSaveStart(false);
        navigate('/collages');
      }, 3000);
    }
  };

  const downloadDialog = (
    <DownloadDialog
      open={actionProps.open}
      onClick={actionProps.onDownloadClick}
      onClose={actionProps.onDownloadClose}
      sizes={constants.paperSizes}
    />
  );

  const saveDialog = (
    <Authenticate>
      <SaveDialog
        open={actionProps.saveOpen}
        onClick={handleSave}
        onClose={actionProps.onSaveClose}
      />
    </Authenticate>
  );

  const saveStatusDialog = (
    <Authenticate>
      <SaveStatusDialog open={actionProps.saveStart} status={saveStatus} />
    </Authenticate>
  );

  const collage = (
    <Collage
      dimensionProps={dimensionProps}
      imageProps={imageProps}
      actionProps={actionProps}
      collageCallback={collageCallback}
      handleRemoveImage={handleRemoveImage}
      options={optionProps.options}
      tree={treeProps.tree}
    />
  );

  const mobileControls = (
    <MobileControls
      optionProps={optionProps}
      actionProps={actionProps}
      dimensionProps={dimensionProps}
      handleClear={clearAll}
    />
  );

  const header = (
    <Header headerRef={dimensionProps.headerRef} name={collageName} />
  );

  const desktopPhotosComp = (
    <DesktopImageOptions
      onDelete={actionProps.onDelete}
      onAdd={dimensionProps.handleImageAddClick}
      clearDelete={actionProps.clearDelete}
    />
  );
  const desktopControls = (
    <DesktopControls
      onRefresh={actionProps.onRefresh}
      photosComp={desktopPhotosComp}
      editComp={<EditControl optionProps={optionProps} />}
      handleClear={clearAll}
      clearDelete={actionProps.clearDelete}
    />
  );

  const isMobile = window.innerWidth < window.innerHeight;
  const controls = isMobile ? mobileControls : desktopControls;

  const onSave = async () => {
    if (saveType === 'new') {
      console.time('saving locally');
      await db.collageDB.details.clear();
      await db.collageDB.images.clear();
      console.timeEnd('saving locally');
      console.time('saving locally state');
      const respdets = await dbutils.save(
        [getDetails(collageName, {})],
        'details'
      );
      const respImgs = await dbutils.save(imageProps.images, 'images');
      console.log('save responses', respdets, respImgs);
      console.timeEnd('saving locally state');
    }
    actionProps.handleSave();
  };

  const actionsComp = isMobile ? (
    <MobileActions
      actionsRef={dimensionProps.actionsRef}
      onDownload={actionProps.handleDownload}
      onSave={onSave}
    />
  ) : (
    <DesktopActions onDownload={actionProps.handleDownload} onSave={onSave} />
  );

  return (
    <>
      <View
        open={actionProps.open}
        saveOpen={actionProps.saveOpen}
        saveStatusOpen={actionProps.saveStart}
        dimensionProps={dimensionProps}
        imageProps={imageProps}
        header={header}
        actions={actionsComp}
        downloadDialog={downloadDialog}
        saveDialog={saveDialog}
        saveStatusDialog={saveStatusDialog}
        collage={collage}
        controls={controls}
        status={status}
        saveType={saveType}
      />
      <input
        ref={dimensionProps.addImageRef}
        type="file"
        hidden
        multiple
        accept="image/*"
        onChange={imageProps.handleLoad}
      />
      {actionProps.generate ? <Loading title="Creating collage ..." /> : null}
    </>
  );
  // return 'base';
}

BaseCollage.propTypes = {
  getCollage: PropTypes.func.isRequired,
  clearCollage: PropTypes.func.isRequired,
  saveType: PropTypes.string.isRequired,
};
